import {useState} from 'react'

const useForm = (callback) => {
  const [input, setInput] = useState('');

  const handleSubmit = (event) => {
    if (event) {
      event.preventDefault();
    }
    callback();
  }

  const handleInputChange = (event) => {
    event.persist();
    setInput(event.target.value);
  }

  return {
    handleSubmit,
    handleInputChange,
    input
  };
}

export default useForm;